.scroll-zone {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: relative;

  .text-current h2 {
    font-size: 80px;
    padding-bottom: 10%;
  }

  .text-current p {
    font-size: 22px;
    float: right;
    font-weight: 200;
  }
}

.scroll-box {
  width: 45%;
  position: relative; 
  overflow: hidden; 
  display: flex; 
  align-items: center; 
  height: 100%;
}